@import url('https://fonts.googleapis.com/css?family=VT323&display=swap');
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap');
body, #root {
  margin:0;
  width: 100%;
  height: 100%;

}

.Typist {
  cursor: default;
}
.intro {
  background-color: #121212;
  width: 100%;
  height: 100%;
}
section {
  color:#f3f3f3;
  width: 95%;
  height: 100%;
  margin: 0 auto;
  background-color: #121212;
}
.cvs {
  position: relative;
  top:50%;
  transform: translateY(-50%);
  text-align: center;
  word-spacing: 3px;
  font-family: 'VT323', monospace;
  font-size: 3em;
  background-color: #121212;
}
.mobile {
  width: 100%;
  height: 100%;
  text-align: center;
  background-color: #f2f2f2;
  display: none;
}
@media ( max-width: 768px ) {
  section {
    display: none;
  }
  .mobile {
    display: block;
  }
  .text_m {
    font-family:'Roboto', sans-serif;
    font-weight: bold;
    font-size:3vh;
    position: relative;
    top:50%;
    text-align: center;
    transform:translateY(-50%);
  }
}
